<template>
    <div class="adv-g__map-container">
        <gmap-map
                v-bind:center="center"
                v-bind:zoom="12"
                v-bind:options="options"
        >
            <gmap-marker
                    v-for="marker in markers"
                    v-bind:key="marker.id"
                    v-bind:icon="markerIcon"
                    v-bind:position="marker">
            </gmap-marker>
        </gmap-map>
    </div>
</template>

<script>

    export default {
        name: "Maps",
        props: {
            points: {
                type: Array,
                default() {
                    return [];
                },
                required: true
            }
        },
        data() {
            return {
                options: {
                    mapTypeControl: false,
                    streetViewControl: false,
                },
                markerIcon: {
                    path: 'M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7-7.75 7-13c0-3.87-3.13-7-7-7zm0 9.5c-1.38 0-2.5-1.12-2.5-2.5s1.12-2.5 2.5-2.5 2.5 1.12 2.5 2.5-1.12 2.5-2.5 2.5z',
                    fillColor: 'rgb(11, 177, 67)',
                    fillOpacity: 1,
                    strokeColor: '#0aa03c',
                    scale: 2.5,
                    size: { height: 24, width: 24 },
                    origin: { x: 0, y: 24 },
                    anchor: { x: 24 / 2, y: 24 }
                }
            }
        },
        computed: {
            markers() {
                return this.points
                    ? this.points.map(p => ({ lat: +p.latitude, lng: +p.longitude, id: p.id }))
                    : [];
            },
            center() {
                return this.markers.length ? this.markers[0] : { lat: 50.45466, lng: 30.5238 };
            }
        }
    }
</script>

<style scoped>
    .adv-g__map-container {
        min-width: 300px;
        min-height: 100%;
        max-height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: stretch;
        flex-grow: 1;
    }

    .adv-g__map-container .vue-map-container {
        min-width: inherit;
        min-height: inherit;
    }
</style>
