<template>
    <modal v-on:close="$emit('close')" class="adv-v__details-modal">
        <template v-slot:title>
            <heading size="h1" v-if="isPromotion">Информация oб aкции</heading>
            <heading size="h1" v-if="isCoupon">Информация о купоне</heading>
            <heading size="h1" v-if="isPromocode">Информация о промокоде</heading>
        </template>
        <template v-slot:body>
            <promotion-information
                    v-if="isPromotion"
                    v-bind:promotion="coupon"
                    v-on:openMap="$emit('openMap')"
            ></promotion-information>
            <coupon-information
                    v-if="isCoupon"
                    v-bind:coupon="coupon"
                    v-on:openMap="$emit('openMap')"
            ></coupon-information>
            <promocode-information
                    v-if="isPromocode"
                    v-bind:promocode="coupon"
            ></promocode-information>
        </template>
        <template v-slot:footer-left>
            <Btn type="primary" v-on:click="downloadFile">
                <template v-slot:icon>
                    <DownloadSvg
                            v-if="!loading"
                            class="adv-btn__icon"
                    ></DownloadSvg>
                    <Spinner
                            v-if="loading"
                            class="adv-btn__icon"
                    ></Spinner>
                </template>
                <template v-slot:name>
                    <span>{{coupon.downloadString}}</span>
                </template>
            </Btn>
        </template>
        <template v-slot:footer-right>
            <Btn type="secondary_outline" v-on:click="$emit('openMap')">
                <template v-slot:icon>
                    <MapSvg class="adv-btn__icon"></MapSvg>
                </template>
                <template v-slot:name>
                    <span>На карте</span>
                </template>
            </Btn>
        </template>
    </modal>
</template>

<script>
    import Modal from "../../ui/Modal";
    import Btn from "../../ui/Button";
    import DownloadSvg from '../../../assets/download.svg';
    import MapSvg from '../../../assets/map.svg';

    import PromotionInformation from "./PromotionInformation";
    import Heading from "../../ui/Heading";
    import { DownloadFileMixin } from '../../../mixins/DownloadFile';
    import Spinner from '../../../assets/anitmated-spinner.svg';
    import CouponInformation from "./CouponInformation";
    import PromocodeInformation from "./PromocodeInformation";

    export default {
        name: "details-information",
        components: {
            PromocodeInformation,
            CouponInformation, Heading, PromotionInformation, Btn, Modal, DownloadSvg,
            Spinner,
            MapSvg
        },
        mixins: [DownloadFileMixin],
        props: {
            coupon: Object
        },
        methods: {
            closeModal() {
                this.$emit('close');
            }
        },
        computed: {
            offerType() {
                return this.coupon.offerType;
            },
            isPromotion() {
                return this.offerType === 'PROMOTION';
            },
            isCoupon() {
                return this.offerType === 'COUPON';
            },
            isPromocode() {
                return this.offerType === 'PROMOCODE';
            }
        }
    }
</script>

<style scoped>
    .adv-v__details-modal {
        width: 500px;
        height: 600px;
    }
</style>
