<template>
    <modal class="adv-g__map-modal" v-on:close="$emit('close')">
        <template v-slot:title>
            <heading size="h1">
                На карте
            </heading>
        </template>
        <template v-slot:body>
            <div class="adv-g__map-modal__body">
                <maps
                        class="adv-g__map-modal__map"
                        v-bind:points="points"
                ></maps>
            </div>
        </template>
    </modal>
</template>

<script>
    import Modal from "../../ui/Modal";
    import Heading from "../../ui/Heading";
    import Maps from "./Maps";
    import * as http from '../../../utils/http';
    import { flatten } from "../../../utils/array";

    export default {
        name: "MapModal",
        components: { Maps, Heading, Modal },
        props: {
            offer: {
                type: Object,
                required: true
            }
        },
        data() {
            return {
                points: []
            };
        },
        mounted() {
            this.loadNetworksPoints();
        },
        methods: {
            loadNetworksPoints() {
                Promise.all(
                    this.offer.networkIds.map(id => this.loadPointForOneNetwork(id))
                )
                    .then(flatten)
                    .then(points => this.points = points);
            },
            loadPointForOneNetwork(networkId) {
                return http.get('https://digitalsavertest.azurewebsites.net/api/gallery/NetworkNetworkPoints', {
                    id: networkId
                })
            }
        }
    }
</script>

<style scoped>
    .adv-g__map-modal {
        width: 800px;
        height: 600px;

    }

    .adv-g__map-modal__body {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    .adv-g__map-modal__map {
        flex-grow: 1;
    }
</style>
